'use client'
import { useState, useEffect, useRef } from 'react'
import { useRouter } from 'next/navigation'
import { FilmIcon, TvIcon, NewspaperIcon } from 'lucide-react'
import Link from 'next/link'
import { SearchIcon } from 'lucide-react'
import '@/app/globals.css';
import LoginModal from '@/components/auth/LoginModal'

export default function Navbar() {
  const [query, setQuery] = useState('')
  const [results, setResults] = useState([])
  const [showDropdown, setShowDropdown] = useState(false)
  const router = useRouter()
  const searchRef = useRef(null)

  useEffect(() => {
    const handler = (e) => {
      if (searchRef.current && !searchRef.current.contains(e.target)) {
        setShowDropdown(false)
      }
    }
    document.addEventListener('mousedown', handler)
    return () => document.removeEventListener('mousedown', handler)
  }, [])

  const handleSearch = async (e) => {
    e.preventDefault()
    if (!query.trim()) return

    const res = await fetch(
      `https://api.themoviedb.org/3/search/multi?api_key=${process.env.NEXT_PUBLIC_TMDB_API_KEY}&language=es-ES&query=${query}`
    )    
    const data = await res.json()
    setResults(data.results || [])
    setShowDropdown(true)
  }

  return (
    <nav className="p-4 flex items-center justify-between relative bg-black">
      {/* Logo */}
      <div className="flex items-center space-x-4">
        <Link href="/" className="block">
          <img
            src="/logo-sinfondo.png"
            alt="Logo"
            className="h-16 object-cover object-center cursor-pointer"
          />
        </Link>
      </div>  

      {/* Barra de búsqueda */}
      <div className="flex-grow max-w-xl mx-8 relative" ref={searchRef}>
        <form onSubmit={handleSearch} className="flex w-full">
          <input
            type="text"
            value={query}
            onChange={(e) => setQuery(e.target.value)}
            placeholder="Buscar películas, series o actores..."
            className="w-full px-4 py-2 bg-[#1F1F1F] text-white rounded-l-3xl border border-[#333] focus:outline-none focus:ring-1 focus:ring-blue-600 placeholder:text-gray-400"
          />
          <button
            type="submit"
            className="bg-blue-600 hover:bg-blue-500 px-4 py-2 rounded-r-3xl flex items-center justify-center"
          >
            <SearchIcon className="text-white" />
          </button>
        </form>


        {showDropdown && results.length > 0 && (
          <div className="absolute top-full left-0 w-full bg-[#121212] text-white mt-2 rounded-2xl shadow-2xl max-h-[550px] overflow-y-auto z-50 border border-[#2a2a2a] backdrop-blur-md no-scrollbar">
            {results.map((item) => (
              <Link
                key={item.id}
                href={`/details/${item.media_type}/${item.id}`}
                onClick={() => {
                  setShowDropdown(false)
                  setQuery('')
                }}
                className="flex items-center gap-4 px-4 py-3 hover:bg-[#1f1f1f] transition-colors cursor-pointer"
              >
                <img
                  src={`https://image.tmdb.org/t/p/w92${item.poster_path || item.profile_path}`}
                  alt={item.title || item.name}
                  className="w-16 h-auto rounded-lg shadow-md"
                />
                <div>
                  <p className="font-semibold text-base">{item.title || item.name || item.original_title}</p>
                  <p className="text-sm text-gray-400 capitalize">
                    {item.media_type === 'movie'
                      ? 'Película'
                      : item.media_type === 'tv'
                      ? 'Serie'
                      : item.media_type === 'person'
                      ? 'Persona'
                      : item.media_type}
                  </p>
                </div>
              </Link>
            ))}
          </div>
        )}
      </div>

      {/* Menú */}
      <div className="flex items-center gap-6 text-white text-sm px-2">
        <a href="/movies" className="hover:text-blue-500 transition-colors">
          <FilmIcon className="w-6 h-6" />
        </a>
        <a href="/series" className="hover:text-blue-500 transition-colors">
          <TvIcon className="w-6 h-6" />
        </a>
        <a href="/news" className="hover:text-blue-500 transition-colors">
          <NewspaperIcon className="w-6 h-6" />
        </a>
        <LoginModal />
      </div>
    </nav>
  )
}
