'use client'

import { getRequestToken } from '@/lib/api/auth'
import { useState } from 'react'

export default function LoginModal() {
  const [loading, setLoading] = useState(false)

  const handleLogin = async () => {
    setLoading(true)
    const token = await getRequestToken()
    if (token) {
      window.location.href = `https://www.themoviedb.org/authenticate/${token}?redirect_to=${window.location.origin}/auth/callback?request_token=${token}`
    }
  }

  return (
    <button
      onClick={handleLogin}
      className="px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded"
    >
      {loading ? 'Redirigiendo...' : 'Iniciar sesión'}
    </button>
  )
}
